import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Header from '../components/Header';
import Loading from '../components/Loading';
import { getUser, updateUser } from '../services/userAPI';

class ProfileEdit extends Component {
  constructor() {
    super();

    this.renderUserInfo = this.renderUserInfo.bind(this);
    this.updateStateInfo = this.updateStateInfo.bind(this);
    this.updateUserInfo = this.updateUserInfo.bind(this);

    this.state = {
      loading: true,
      name: '',
      email: '',
      image: '',
      description: '',
    };
  }

  async componentDidMount() {
    const userInfo = await getUser();
    const { name, email, image, description } = userInfo;
    this.setState({
      loading: false,
      name,
      email,
      image,
      description,
    });
  }

  enableSaveButton({ target }) {
    const array = [...target.parentElement.children];
    if (array.some((element) => element.value === '')) {
      target.parentElement.lastChild.setAttribute('disabled', 'disabled');
    } else {
      target.parentElement.lastChild.removeAttribute('disabled');
    }
  }

  updateStateInfo({ target }) {
    this.setState({
      [target.name]: target.value,
    }, () => this.updateUserInfo());
  }

  async updateUserInfo() {
    const { name, email, image, description } = this.state;
    await updateUser({
      name,
      email,
      image,
      description,
    });
  }

  renderUserInfo() {
    const { name, email, image, description } = this.state;
    const { history } = this.props;
    return (
      <form
        onChange={ this.enableSaveButton }
      >
        <input
          name="name"
          type="text"
          data-testid="edit-input-name"
          defaultValue={ name }
          required
          onChange={ this.updateStateInfo }
          className="form-control"
          placeholder="Nome"
        />
        <input
          name="image"
          type="text"
          data-testid="edit-input-image"
          defaultValue={ image }
          required
          onChange={ this.updateStateInfo }
          className="form-control"
          placeholder="Url da sua foto"
        />
        <input
          name="email"
          type="email"
          data-testid="edit-input-email"
          defaultValue={ email }
          required
          onChange={ this.updateStateInfo }
          className="form-control"
          placeholder="Email"
        />
        <textarea
          name="description"
          data-testid="edit-input-description"
          defaultValue={ description }
          required
          onChange={ this.updateStateInfo }
          className="form-control"
          placeholder="Sobre você"
        />
        <input
          data-testid="edit-button-save"
          type="button"
          onClick={ () => history.push('/profile') }
          value="Salvar"
          className="form-control btn btn-dark"
        />
      </form>
    );
  }

  render() {
    const { loading, image } = this.state;
    return (
      <div data-testid="page-profile-edit" className="container">
        {image ? <Header userImage={ image } /> : <Header />}
        <h1>Atualize seu perfil!</h1>
        {loading ? <Loading /> : this.renderUserInfo()}
      </div>
    );
  }
}

ProfileEdit.propTypes = {
  history: PropTypes.objectOf.isRequired,
};

export default ProfileEdit;
