import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Header from '../components/Header';
import Loading from '../components/Loading';
import { getUser } from '../services/userAPI';

class Profile extends Component {
  constructor() {
    super();

    this.renderUserInfo = this.renderUserInfo.bind(this);

    this.state = {
      loading: true,
      userInfo: { image: '' },
    };
  }

  async componentDidMount() {
    const userInfo = await getUser();
    this.setState({
      userInfo,
      loading: false,
    });
  }

  renderUserInfo() {
    const { userInfo: { name, email, image, description } } = this.state;
    return (
      <div className="user-info">
        <img src={ image } alt="User" data-testid="profile-image" />
        <p>{name}</p>
        <p>{email}</p>
        <p>{description}</p>
        <Link to="profile/edit" className="link">Editar perfil</Link>
      </div>
    );
  }

  render() {
    const { loading, userInfo: { image } } = this.state;
    return (
      <div data-testid="page-profile" className="container">
        {image ? <Header userImage={ image } /> : <Header />}
        {loading ? <Loading /> : this.renderUserInfo()}
      </div>
    );
  }
}

export default Profile;
