import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Header from '../components/Header';
import searchAlbumsAPI from '../services/searchAlbumsAPI';
import Loading from '../components/Loading';
import { getUser } from '../services/userAPI';

class Search extends Component {
  constructor() {
    super();

    this.enableSearchButton = this.enableSearchButton.bind(this);
    this.getSearchResults = this.getSearchResults.bind(this);
    this.notLoading = this.notLoading.bind(this);
    this.getUserPicture = this.getUserPicture.bind(this);

    this.state = {
      searchButtonDisabled: true,
      artist: '',
      loading: false,
      image: '',
    };
  }

  componentDidMount() {
    this.getUserPicture();
  }

  async getUserPicture() {
    const userInfo = await getUser();
    const { image } = userInfo;
    this.setState({ image });
  }

  async getSearchResults() {
    this.setState({ loading: true });
    const { artist } = this.state;
    this.setState({
      searchResults: await searchAlbumsAPI(artist),
      loading: false,
    });
  }

  enableSearchButton({ target }) {
    const minCaracteres = 2;
    if (target.value.length >= minCaracteres) {
      this.setState({
        searchButtonDisabled: false,
        artist: target.value,
      });
    } else {
      this.setState({
        searchButtonDisabled: true,
        artist: target.value,
      });
    }
  }

  displaySearchInput() {
    const { searchButtonDisabled } = this.state;
    return (
      <div className="search-input">
        <input
          className="form-control"
          type="text"
          data-testid="search-artist-input"
          onChange={ this.enableSearchButton }
        />
        <button
          type="button"
          data-testid="search-artist-button"
          disabled={ searchButtonDisabled }
          onClick={ this.getSearchResults }
          className="btn btn-dark"
        >
          Pesquisar
        </button>
      </div>
    );
  }

  notLoading() {
    const { searchResults, artist } = this.state;
    if (!searchResults) return this.displaySearchInput();
    if (searchResults.length === 0) {
      return (
        <div className="search-bigger-container">
          {this.displaySearchInput()}
          <span>Nenhum álbum foi encontrado</span>
        </div>
      );
    }
    return (
      <div className="search-bigger-container">
        {this.displaySearchInput()}
        <p>
          Exibindo resultados para:
          {' '}
          {`"${artist}"`}
        </p>
        <div className="search-results">
          {searchResults.map((result) => (
            <div
              key={ result.collectionId }
              className="album-card"
            >
              <div className="album-info">
                <h6>{result.collectionName}</h6>
                <Link
                  to={ `/album/${result.collectionId}` }
                  data-testid={ `link-to-album-${result.collectionId}` }
                  className="link"
                >
                  Mais Informações
                </Link>
              </div>
              <img src={ result.artworkUrl100 } alt={ result.collectionName } />
            </div>))}
        </div>
      </div>
    );
  }

  render() {
    const { loading, image } = this.state;

    return (
      <div data-testid="page-search" className="container">
        {image ? <Header userImage={ image } /> : <Header />}
        {loading ? <Loading /> : this.notLoading()}
      </div>
    );
  }
}

export default Search;
