import React, { Component } from 'react';
import Header from '../components/Header';
import { getFavoriteSongs } from '../services/favoriteSongsAPI';
import MusicCard from '../components/MusicCard';
import { getUser } from '../services/userAPI';

class Favorites extends Component {
  constructor() {
    super();

    this.loadFavSongs = this.loadFavSongs.bind(this);
    this.getUserPicture = this.getUserPicture.bind(this);

    this.state = {
      favSongs: [],
      image: '',
    };
  }

  componentDidMount() {
    this.loadFavSongs();
    this.getUserPicture();
  }

  componentDidUpdate() {
    this.loadFavSongs();
  }

  async getUserPicture() {
    const userInfo = await getUser();
    const { image } = userInfo;
    this.setState({ image });
  }

  async loadFavSongs() {
    const favSongs = await getFavoriteSongs();
    this.setState({ favSongs });
  }

  // async removeAllFavSongs() {
  //   const favSongs = await getFavoriteSongs();
  //   favSongs.forEach((song) => removeSong(song));
  // }

  render() {
    const { favSongs, image } = this.state;
    return (
      <div data-testid="page-favorites" className="container">
        {image ? <Header userImage={ image } /> : <Header />}
        <MusicCard songsInfo={ favSongs } />
      </div>
    );
  }
}

export default Favorites;
