import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Header from '../components/Header';
import getMusics from '../services/musicsAPI';
import MusicCard from '../components/MusicCard';

class Album extends Component {
  constructor() {
    super();

    this.state = {
    };
  }

  async componentDidMount() {
    const { match: { params: { id } } } = this.props;
    const albumInfo = await getMusics(id);
    this.setState({
      albumInfo: albumInfo.filter((item) => item.kind === 'song'),
      artistName: albumInfo[0].artistName,
      albumName: albumInfo[0].collectionName,
    });
  }

  render() {
    const { artistName, albumName, albumInfo } = this.state;
    return (
      <div data-testid="page-album" className="container">
        <Header />
        <h1>{`${artistName} - ${albumName}`}</h1>
        {albumInfo && <MusicCard songsInfo={ albumInfo } />}
      </div>
    );
  }
}

Album.propTypes = {
  match: PropTypes.objectOf.isRequired,
};

export default Album;
