import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { addSong, getFavoriteSongs, removeSong } from '../services/favoriteSongsAPI';
import Loading from './Loading';

class MusicCard extends Component {
  constructor() {
    super();

    this.state = {
      loading: false,
      favoriteSongs: [],
    };
  }

  async componentDidMount() {
    const favSongs = await getFavoriteSongs();
    this.setState({ favoriteSongs: favSongs });
    console.log(favSongs);
  }

  async componentDidUpdate() {
    const favSongs = await getFavoriteSongs();
    this.setState({ favoriteSongs: favSongs });
  }

  async addFavoriteSong(song) {
    this.setState({ loading: true });
    await addSong(song);
    this.setState({ loading: false });
  }

  async removeFavoriteSong(song) {
    this.setState({ loading: true });
    await removeSong(song);
    this.setState({ loading: false });
  }

  renderFavoriteInput(song) {
    const { favoriteSongs } = this.state;
    return (
      <label htmlFor="favorite-input">
        Favorita
        {' '}
        {favoriteSongs.some((favSong) => favSong.trackId === song.trackId) ? <input
          type="checkbox"
          name="favorite-input"
          id="favorite-input"
          data-testid={ `checkbox-music-${song.trackId}` }
          onClick={ () => this.removeFavoriteSong(song) }
          checked
          className="form-check-input"
        /> : <input
          type="checkbox"
          name="favorite-input"
          id="favorite-input"
          data-testid={ `checkbox-music-${song.trackId}` }
          onClick={ () => this.addFavoriteSong(song) }
          className="form-check-input"
        />}
      </label>
    );
  }

  render() {
    const { songsInfo } = this.props;
    const { loading } = this.state;
    return (
      <div className="songs">
        {loading && <Loading />}
        {songsInfo.map((song) => (
          <div key={ song.trackId } className="song-card">
            <div className="song-name"><h5>{song.trackName}</h5></div>
            <audio
              data-testid="audio-component"
              src={ song.previewUrl }
              controls
            >
              <track kind="captions" />
              O seu navegador não suporta o elemento
              {' '}
              <code>audio</code>
              .
            </audio>
            {this.renderFavoriteInput(song)}
          </div>
        ))}
      </div>
    );
  }
}

MusicCard.propTypes = {
  songsInfo: PropTypes.objectOf.isRequired,
};

export default MusicCard;
