import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { createUser } from '../services/userAPI';
import Loading from '../components/Loading';

class Login extends Component {
  constructor() {
    super();

    this.enableLoginButton = this.enableLoginButton.bind(this);
    this.userLogin = this.userLogin.bind(this);

    this.state = {
      loginButtonDisabled: true,
      userName: '',
      loading: false,
    };
  }

  enableLoginButton({ target }) {
    const minCaracteres = 3;
    if (target.value.length >= minCaracteres) {
      this.setState({
        loginButtonDisabled: false,
        userName: target.value,
      });
    } else {
      this.setState({
        loginButtonDisabled: true,
        userName: target.value,
      });
    }
  }

  async userLogin() {
    const { userName } = this.state;
    const { history } = this.props;
    this.setState({ loading: true });
    await createUser({ name: userName });
    this.setState({ loading: false }, () => history.push('/search'));
  }

  loginInputDisplay() {
    const { loginButtonDisabled } = this.state;
    return (
      <div className="login-container">
        <input
          type="text"
          data-testid="login-name-input"
          onChange={ this.enableLoginButton }
          className="form-control form-control-lg"
        />
        <button
          type="button"
          data-testid="login-submit-button"
          disabled={ loginButtonDisabled }
          onClick={ this.userLogin }
          className="btn btn-dark"
        >
          Entrar
        </button>
      </div>
    );
  }

  render() {
    const { loading } = this.state;
    return (
      <div data-testid="page-login">
        {loading ? <Loading /> : this.loginInputDisplay() }
      </div>
    );
  }
}

Login.propTypes = {
  history: PropTypes.objectOf.isRequired,
};

export default Login;
